import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import Img from "gatsby-image"

export default ({data}) => (
    <Layout>
    <div>
        <div id="main">
        
        <span class="avatar"> <Img fluid={data.file.childImageSharp.fixed} style={{
            width: "150px",
            height: "150px"
        }} /> </span>
        <h1>Dattatraya Mule</h1>
        <p>Full Stack Architect</p>
        <p>ES6/TypeScript/JavaScript</p>
        <p>AngularJS/ReactJS</p>
        <p>Java/NodeJS/.Net</p>
        <p>dattatrayam@gmail.com</p>
        <p>Want to offer me a job? 
        <br/>
        <b><a href="https://docs.google.com/forms/d/e/1FAIpQLSergC9f4TiDjsDBO7dpI6iS-0bH7kBHZAuaxfC4OF_UWdu2iw/viewform">Fill this out</a></b>.</p>
        
        </div>
    </div>
   </Layout>
  )

  export const query = graphql`
  query {
    file(relativePath: { eq: "photo.JPG" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 150, height: 150) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`